* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
		'Lucida Sans', Arial, sans-serif;
}

body {
	background: linear-gradient(
		90deg,
		rgb(16, 255, 199) 0%,
		rgb(159, 255, 100) 100%
	);
}
.todo-app {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: 520px;
	min-height: 600px;
	background: #000000dc;
	text-align: center;
	margin: 128px auto;
	border-radius: 10px;
	padding-bottom: 32px;
	filter: drop-shadow(0 0 120px rgb(147, 20, 220));
}

h1 {
	margin: 32px 0;
	color: #fff;
	font-size: 24px;
}

.complete {
	text-decoration: line-through;
	opacity: 0.4;
}

.todo-form {
	margin-bottom: 32px;
}

.todo-input {
	padding: 14px 32px 14px 16px;
	border-radius: 4px 0 0 4px;
	border: 2px solid #5d0cff;
	outline: none;
	width: 320px;
	background: transparent;
	color: #fff;
}

.todo-input::placeholder {
	color: #e2e2e2;
}

.todo-button {
	padding: 16px;
	border: none;
	border-radius: 0 4px 4px 0;
	cursor: pointer;
	outline: none;
	background: linear-gradient(
		90deg,
		rgba(93, 12, 255, 1) 0%,
		rgba(155, 0, 250, 1) 100%
	);
	color: #fff;
	text-transform: capitalize;
}

.todo-input.edit {
	border: 2px solid #149fff;
}

.todo-button.edit {
	background: linear-gradient(
		90deg,
		rgba(20, 159, 255, 1) 0%,
		rgba(17, 122, 255, 1) 100%
	);
	padding: 16px 22px;
}

.todo-container {
	display: flex;
	flex-direction: row;
	position: relative;
}

.todo-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 4px auto;
	color: #fff;
	background: linear-gradient(
		90deg,
		rgba(255, 84, 17, 1) 0%,
		rgba(255, 118, 20, 1) 100%
	);

	padding: 16px;
	border-radius: 5px;
	width: 90%;
}

.todo-row:nth-child(3n + 1) {
	background: linear-gradient(
		90deg,
		rgb(100, 0, 250) 0%,
		rgb(190, 12, 255) 100%
	);
}

.todo-row:nth-child(3n + 2) {
	background: linear-gradient(
		90deg,
		rgb(13, 180, 63) 0%,
		rgb(140, 221, 1) 110%
	);
}

.icons {
	display: flex;
	align-items: center;
	font-size: 24px;
	cursor: pointer;
}

.delete-icon {
	margin-right: 5px;
	color: #fff;
}

.edit-icon {
	color: #fff;
}
